<template>
  <div class="app-container">
    <h1>Scryfall Query Builder</h1>
    <div>
      <div class="editor-container">
        <div ref="editor"></div>
      </div>
      <div class="editor-actions">
        <button class="button" @click="generateScryfallQuery">Generate Query</button>
        <button class="button" v-show="showSaveSnippet" @click="saveSelection">Save snippet</button>
      </div>
    </div>

    <div class="output">
      <div>
        <div><strong>Scryfall Query:</strong></div>
        <div class="query-box">{{ scryfallQuery }}</div>
      </div>
      <div>
        <div><strong>Link:</strong></div>
        <div class="query-box">
          <a :href="scryfallLink" target="_blank">{{ scryfallLink }}</a>
        </div>
      </div>
    </div>

    <div v-if="parseError" class="error">
      <span>An error occurred</span>
      {{ parseError }}
    </div>

    <div class="snippets-container">
      <div class="snippet-container-header">
        <div><button class="green snippets-container-visible-button" @click="toggleSnippets">{{ snippetsVisible ? "-" : "+" }}</button></div>
        <div><h3>Snippets</h3></div>
      </div>

      <div v-if="snippetsVisible">         
        <div>
          <div v-for="(snippet, index) in snippets" :key="index" class="snippet-container">
            <div class="snippet-header">
              <span class="snippet-title">{{ snippet.name }}</span>
              <div class="snippet-actions">
                <button class="green" @click="insertSnippet(snippet.content)">Insert</button>
                <button class="red" @click="removeSnippet(snippet.name)">Delete</button>
              </div>
            </div>
            <div class="snippet-content">
              {{ snippet.content }}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { onMounted, ref, computed } from "vue";
import { EditorView, basicSetup } from "codemirror";
import { EditorState } from "@codemirror/state";
import { oneDark } from "@codemirror/theme-one-dark";
import { scrydslTheme } from "./scrydsl-theme";
import { scrydsl } from './scrydsl-lang';
import { convertToScryfallQuery } from './scrydsl-converter';
import { syntaxTree } from "@codemirror/language";

export default {
  name: "LightweightEditor",
  props: {
    modelValue: String,
  },
  setup(props, { emit }) {
    const editor = ref(null);
    const query = ref('');
    const scryfallLink = ref('');
    const parseError = ref('');
    const scryfallQuery = ref('');
    const snippetsVisible = ref(false);
    let editorView = null;
    let contextMenu = null;
    const selection = ref({from:0, to:0});

    const showSaveSnippet = computed(() => {
      return selection.value.from !== selection.value.to;
    });

    const getSnippets = () => {
      return JSON.parse(localStorage.getItem("snippets")) || [];
    };

    const snippets = ref(getSnippets());
    const saveSnippets = () => {
      localStorage.setItem("snippets", JSON.stringify(snippets.value));
    }

    const saveSnippet = (name, content) => {
      snippets.value.push({name, content});
      saveSnippets();
    };

    const saveSelection = () => {
      const selectedText = editorView.state.sliceDoc(selection.value.from, selection.value.to);
      const snippetName = prompt("Enter a name for this snippet:");
      if (snippetName && selectedText.trim()) {
        saveSnippet(snippetName, selectedText);
        snippets.value = getSnippets(); 
      }
      if (contextMenu && document.body.contains(contextMenu)) {
        document.body.removeChild(contextMenu);
      }
    };

    const deleteSnippet = (name) => {
      const index = snippets.value.findIndex(ele => ele.name === name);
      if (index > -1) {
        snippets.value.splice(index, 1);
        saveSnippets();
      }
    };

    const removeSnippet = (name) => {
      deleteSnippet(name);
    };

    const insertSnippet = (content) => {
      const from = editorView.state.selection.main.head;
      let insert = content;
      if(from > 1) {        
        const docText = editorView.state.doc.toString();
        const prevChar = from > 0 ? docText[from - 1] : null;
        if (prevChar === " ") {
          insert = `and ${content}`;
        } else if (prevChar !== "(") {
          insert = ` and ${content}`;
        }
      }

      editorView.dispatch({
        changes: { from: from, insert: insert },
        selection: { anchor: from + insert.length, head: from + insert.length }
      });
    };

    const toggleSnippets = () => {
      snippetsVisible.value = !snippetsVisible.value;
    };

    onMounted(() => {
      const state = EditorState.create({
        doc: props.modelValue || "",
        extensions: [
          basicSetup,
          scrydsl(),
          scrydslTheme,
          oneDark,
          EditorView.updateListener.of((update) => {
            if (update.changes) {
              const newValue = update.state.doc.toString();
              query.value = newValue;
              selection.value =  { from: editorView.state.selection.main.from, to: editorView.state.selection.main.to };
            }
          }),
        ],
      });

      editorView = new EditorView({
        state,
        parent: editor.value,
      });
    });

    const generateScryfallQuery = () => {
      try {
        const tree = syntaxTree(editorView.state);
        if (!tree) {
          throw new Error('Invalid query structure.');
        }
        const convertedQuery = convertToScryfallQuery(tree, editorView.state.doc);
        if (!convertedQuery) throw new Error('Could not generate the scryfall query');

        scryfallQuery.value = convertedQuery;
        scryfallLink.value = `https://scryfall.com/search?q=${encodeURIComponent(convertedQuery)}`;
        parseError.value = '';
      } catch (error) {
        scryfallQuery.value = '';
        scryfallLink.value = '';
        parseError.value = error.message;
      }
    };

    return {
      editor,
      scryfallQuery,
      scryfallLink,
      parseError,
      generateScryfallQuery,
      removeSnippet,
      insertSnippet,
      snippets,
      toggleSnippets,
      snippetsVisible,
      showSaveSnippet,
      saveSelection
    };
  },
};
</script>


<style>
.app-container {
  padding: 10px;
}

.app-container h1 {
  font-size: 1.5em;
}

.editor-container {
  position: relative;
  margin-bottom: 10px;
}

.editor-actions {
  display: flex;
  gap: 5px;
}

.button {
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  cursor: pointer;
  border-radius: 5px;
}

.button:hover {
  background-color: #0056b3;
}

.output {
  margin-top: 20px;
  padding: 15px;
  border: 1px solid #44475a;
  border-radius: 8px;
  font-size: 14px;
  color: #f8f8f2;
}

.output div {
  margin-bottom: 10px;
}

.output .query-box {
  background-color: #282c34;
  padding: 10px;
  border-radius: 5px;
  white-space: pre-wrap;
  overflow-x: auto;
  color: #f8f8f2;
}

.output a {
  color: #8be9fd;
  text-decoration: none;
  font-weight: bold;
}

.output a:hover {
  text-decoration: underline;
}

.error {
  margin-top: 20px;
  padding: 10px;
  background-color: #ff5555;
  color: #f8f8f2;
  border: 1px solid #ff3333;
  border-radius: 8px;
  font-size: 14px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.error::before {
  content: "";
  font-size: 16px;
}

.snippets-container {
  margin-top: 20px;
  padding: 15px;
  border: 1px solid #44475a;
  border-radius: 8px;
  color: #f8f8f2;

  margin-top: 20px;
  padding: 15px;
  border: 1px solid #44475a;
  border-radius: 8px;
  font-size: 14px;
  color: #f8f8f2;
}

.snippets-container-visible-button {
  width: 41px;
}

.snippets-container h3 {
  font-size: 1.4em;
  margin: 0px;
  text-align: center;
}

.snippets-container input,
.snippets-container textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  background-color: #282c34;
  color: #f8f8f2;
  border: 1px solid #6272a4;
  border-radius: 5px;
  font-size: 14px;
}


.snippets-container button {
  padding: 10px 15px;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s;
}


.snippet-container {
  margin-top: 10px;
  padding: 10px;
  border: 1px solid #44475a;
  border-radius: 8px;
}

.snippet-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.snippet-title {
  font-size: 1.1em;
  font-weight: bold;
  color: #f8f8f2;
}

.snippet-actions button {
  color: #fff;
  font-size: 12px;
  padding: 5px 10px;
  margin-left: 10px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.snippet-content {
  background-color: #353b45;
  padding: 10px;
  border-radius: 5px;
  font-size: 14px;
  white-space: pre-wrap;
  word-wrap: break-word;
}

.snippet-container-header {
  display: flex;
  flex: 1;
  align-items: center;
  gap: 10px;
}

.snippet-container-header button {
  background-color: transparent;
  font-size: 18px;
  border: none;
  cursor: pointer;
  transition: color 0.3s;
}

.snippet-container-header button:hover {
  color: #50fa7b;
}

.snippet-container-header h3 {
  flex-grow: 1;
  font-size: 1.2em;
  text-align: center;
}

.snippets-container.collapsed .snippet-container {
  display: none;
}

.snippets-container.collapsed .snippet-container-header button {
  transform: rotate(90deg);
}

.snippets-container.collapsed .snippet-container-header h3 {
  font-size: 1.2em;
}

.snippets-empty {
  margin-top: 10px;
}

</style>

<style>
.custom-context-menu {
  background-color: #282c34;
  border: 1px solid #44475a;
  padding: 5px;
  color: #f8f8f2;
  border-radius: 5px;
  font-size: 14px;
  z-index: 9999;
  cursor: pointer;
}
.custom-context-menu div {
  padding: 5px;
}
.custom-context-menu div:hover {
  background-color: #44475a;
}
</style>