import { grammar } from "./grammar";
import { data } from "./data";
import { syntaxTree } from "@codemirror/language";

const keywords = [...new Set(Object.entries(grammar.fields).map(([k,v]) => v.keywords).flat().filter(s => s))];
const fieldsByName = Object.entries(grammar.fields)
  .reduce((acc, [k,v]) => {       
    v.name = v.mappedField ? v.mappedField : k;
    const field = v;
    acc[k] = field;
    if(v.shorthand) {
      acc[v.shorthand] = v;
    }
    return acc;
  }, {}); 

const keywordDetails = {
  "is": "exactly the specified value(s)",
  "includes": "has the specified value(s)",
  "for": "usable for specified value(s)"
};

const fieldSuggestions =  Object.entries(grammar.fields)
  .map(([k,v]) => ({ 
    label: k, 
    type: "class",
    detail: v.detail
  }));
const logicalOperatorSuggestions = [ {label: "and", type: "class" }, {label: "or", type: "class" }];

function buildValueSuggestions (fieldName)  {
  const field = fieldsByName[fieldName]?.name ?? fieldName;  
  const section = { name: `${["color", "identity"].includes(field) ? "color" : field} values` };
  if(field === 'keyword') {
    return data.keywordAbilities.map(s => ({ label:s, type: "class", section: section }));
  }

  if(field === 'color' || field === 'identity') {
    return data.colorSuggestions.map(s => ({
      label: s.value,
      displayLabel: s.label,
      type: "class", 
      section: section
    }));
  }

  if(data[field]) {    
    return data[field].map(s => ({
      label: s,
      type: "class",      
      section: section
    }));
  }

  return [];
}

function getPreviousValidContext(node) {
  let child = null;
  let next = node.firstChild;
  for(;;) {
    if(!next || next.name === ""){
      break;
    }
    child = next;
    next = next.nextSibling;
  }
  if(child && child.name !== "") {
    return child;
  }
  return null;
} 

function getPreviousNamed(node, names) {
  let curr = node;
  while (curr) {
    if(names.includes(curr.name))
      break;
    curr = curr.prevSibling;
  }
  return curr;
}

function getFieldValue(node, context) {
  let field = context.state.sliceDoc(node.from, node.to);
  if(fieldsByName[field]?.mappedField)
    field = fieldsByName[field].mappedField;
  return field;
}

function buildSuggestionsFromNode(node, context, word) {  
  if(!node?.name || node.name === 'Program' ) {
    return logicalOperatorSuggestions;
  }
  else if(node.name === "Expression"){
    const next = node.firstChild;
    if(!next?.name || next.name === "")
      return fieldSuggestions;
    if(next.name === "Expression")
      return buildSuggestionsFromNode(next.lastChild, context, word);
  }
  else if(node.name === "UnaryExpression" || node.name === "BinaryExpression" || node.name === "")
  {
    return fieldSuggestions;
  }
  else if(node.name === 'Term' || node.name === 'SingleTerm') {
    const previous = getPreviousValidContext(node);
    if (!previous?.name) {
      return fieldSuggestions;
    }
    if(previous.name === "Field") {
      const field = getFieldValue(previous, context);        
      return fieldsByName[field].keywords.map(s => ({label:s, type: "keyword", detail: keywordDetails[s] }));
    } else if (previous.name === "SingleField") {        
      const field = getFieldValue(previous, context);  
      return buildValueSuggestions(field);      
    } else if (previous.name === "Keyword" || previous.name === "Value") {
      const fieldNode = getPreviousNamed(previous, ["Field", "SingleField"]);    
      const field = getFieldValue(fieldNode, context); 
      return buildValueSuggestions(field);
    } 
  }
}

function buildSuggestions(context, word) {
  if(context.pos === 0 || word?.from === 0) {
    return fieldSuggestions
  }

  const tree = syntaxTree(context.state);
  console.log("tree", tree.toString());
  if(word && word.from > 0) {
    const node = tree.resolveInner(word.from, -1);
    return buildSuggestionsFromNode(node, context, word);
  }
  return null;
}

export function scrydslCompletions(context) {
  const word = context.matchBefore(/\w*/);
  const suggestions = buildSuggestions(context, word);
  if(!suggestions || suggestions.length === 0) {
    return null;
  }
  return {
    from: word?.from ?? context.pos,
    options: suggestions
  }
}